import { applyMiddleware, combineReducers, legacy_createStore } from 'redux';
import { taskReducerType, tasksReducer } from './tasksReducer';
import { todolistsReducer, todolistsReducerType } from './todolistsReducer';
import { ThunkAction, thunk } from 'redux-thunk';
import { ActionAppType, appReducer } from './appReducer';
import { authReducer, authReducerType } from './authReducer';

//  reducer-   combineReducers,
//      -
const rootReducer = combineReducers({
   tasks: tasksReducer,
   todolists: todolistsReducer,
   app: appReducer,
   auth:authReducer,
})
//   store
export const store = legacy_createStore<any, any>(rootReducer, applyMiddleware(thunk));//   undefined
//      
export type AppRootStateType = ReturnType<typeof rootReducer>

//    ,  
export type AppThunk<ReturnType = void> = ThunkAction<ReturnType, AppRootStateType, unknown, AppAllReducerType>

//   reducer
export type AppAllReducerType = taskReducerType | todolistsReducerType | ActionAppType | authReducerType
//  ,         store   
// @ts-ignore
window.store = store;
